.homePage {
  display: flex;
  flex-direction: column;
  align-items: center;

  & > * {
    margin-bottom: 60px;
  }

  &__introContainer {
    margin-top: 100px;
  }

  & .typer {
    font-size: 2rem;
    font-weight: bold;

    &__caret {
      width: .2rem;
      height: 2.4rem;
      margin-bottom: 0.4rem;
    }
  }
}

