.typer {
    display: flex;
    align-items: flex-end;
    white-space: pre;

    &__caret {
        width: .14rem;
        height: 1.2rem;
        background-color: black;
        margin-left: .2rem;
        margin-bottom: 0.1rem;

        &--hide {
            opacity: 0;
        }
    }
}