@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{box-sizing:border-box}html,body{margin:0;padding:0;overflow:hidden}a{scroll-behavior:smooth}input:focus,textarea:focus,select:focus,button:focus{outline:none}body{font-family:"Roboto Slab",serif}.input__field{background-color:#caeaee;color:#087e8b;border:none;padding:4px 10px}.typer{display:flex;align-items:flex-end;white-space:pre}.typer__caret{width:.14rem;height:1.2rem;background-color:black;margin-left:.2rem;margin-bottom:0.1rem}.typer__caret--hide{opacity:0}.playground{max-width:100vw;width:400px}.playground__preview{display:flex;justify-content:center;align-items:center;background-color:#087e8b;color:#f5f5f5;height:80px}.playground__variables{padding:10px;border:2px solid #087e8b}.playground__variables>*{display:flex;justify-content:space-between;margin-bottom:10px}.homePage{display:flex;flex-direction:column;align-items:center}.homePage>*{margin-bottom:60px}.homePage__introContainer{margin-top:100px}.homePage .typer{font-size:2rem;font-weight:bold}.homePage .typer__caret{width:.2rem;height:2.4rem;margin-bottom:0.4rem}.page{background-color:#f5f5f5;color:#087e8b;width:100vw;min-height:100vh}

