.playground {
  max-width: 100vw;
  width: 400px;


  &__preview {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: $color-primary;
    color: $color-light;
    height: 80px;
    // padding: 16px 0;
  }

  &__variables {
    padding: 10px;
    border: 2px solid $color-primary;

    & > * {
        display: flex;
        justify-content: space-between;
        margin-bottom: 10px;
    }
  }
}
